import {ElementRef, Injectable} from '@angular/core';
import {ConnectedPosition} from '@angular/cdk/overlay';
import {LinkEditorComponent} from './link-editor.component';
import {OverlayPanel} from '@common/core/ui/overlay-panel/overlay-panel.service';
import {OverlayPanelRef} from '@common/core/ui/overlay-panel/overlay-panel-ref';

@Injectable({
    providedIn: 'root'
})
export class LinkEditor {
    public overlayRef: OverlayPanelRef;

    constructor(private overlayPanel: OverlayPanel) {}

    public open(node: HTMLLinkElement) {
        const bottom = [
            {originX: 'center', originY: 'bottom', overlayX: 'center', overlayY: 'top', offsetY: 25, offsetX: 380}, // bottom
            {originX: 'end', originY: 'bottom', overlayX: 'end', overlayY: 'top', offsetY: 25, offsetX: 380}, // bottom
            {originX: 'start', originY: 'bottom', overlayX: 'start', overlayY: 'top', offsetY: 25, offsetX: 380}, // bottom
            {originX: 'center', originY: 'top', overlayX: 'center', overlayY: 'bottom', offsetY: -25, offsetX: 380}, // top
        ] as ConnectedPosition[];

        this.overlayRef = this.overlayPanel.open(LinkEditorComponent, {
            position: bottom,
            origin: new ElementRef(node),
            data: {node}
        });
    }

    public close() {
        this.overlayRef && this.overlayRef.close();
    }
}
